
import Vue from 'vue';

export enum ELoaderType {
    Default = 'default',
    DefaultInner = 'default-inner',
    Alternate01 = 'alternate-01',
    Alternate02 = 'alternate-02',
    Coffee = 'coffee',
    Chiptune = 'chiptune'

}

export default Vue.extend({
    name: 'Preloader',
    data() {
        return {}
    },
    props: {
        full: {
            type: Boolean,
            default: false
        },
        fill: {
            type: Boolean,
            default: false
        },
        loader_type: {
            type: String,
            default: ELoaderType.Alternate01
        }
    },
    mounted() { },
    methods: {}
});
